import axios from 'axios'

//获取热门搜索的数据
export function getHotSearches (){
	let url = "static/hot-search.json"
	return new Promise((success,error) => {
		//传递的参数必须使用{}括起来
		axios.get(url)
		.then((res) => {
			success(res);
		})
		.catch((err) => {
			error(err);
		})
	})
}

